.about {
    @include container();

    display: flex;
    align-items: flex-start;
    padding-top: 120px;
    padding-bottom: 120px;

    @include vp-1023 {
        padding-top: 100px;
        padding-bottom: 100px;
    }

    @include vp-767 {
        flex-direction: column;
        padding-top: 65px;
        padding-bottom: 65px;
    }
}

.about__title {
    font-size: 28px;
    line-height: 42px;
    font-weight: 600;
    margin: 0;
    margin-top: 46px;
    margin-bottom: 24px;

    @include vp-1023 {
        font-size: 26px;
        line-height: 36px;
        margin-bottom: 15px;
        margin-top: -9px;
    }

    @include vp-767 {
        font-size: 24px;
        line-height: 34px;
        margin-top: -8px;
    }
}

.about__description {
    margin: 0;
    font-size: 16px;
    line-height: 24px;

    @media (max-width: $tablet-width-max) and (min-width: $tablet-width-min) {
        margin-bottom: -6px;
    }
}

.about__picture {
    margin-left: 85px;
    flex-shrink: 0;

    @include vp-1023 {
        margin-left: 30px;
    }

    @include vp-767 {
        margin: 0 auto;
        margin-top: 33px;
    }
}

.about__image {
    display: block;
    width: 560px;
    height: 301px;
    object-fit: cover;

    @include vp-1023 {
        width: 324px;
        height: 264px;
    }

    @include vp-767 {
        width: 290px;
        height: 186px;
    }
}