.products {
    @include container();
    width: 100%;
    background-color: $color-white;
    padding-top: 120px;
    padding-bottom: 120px;

    @include vp-1023 {
        padding-top: 100px;
        padding-bottom: 100px;
    }

    @include vp-767 {
        padding-top: 65px;
        padding-bottom: 65px;
    }
}

.products__title {
    text-align: center;
    font-size: 28px;
    line-height: 42px;
    font-weight: 600;
    margin: 0;
    margin-top: -11px;
    margin-bottom: 50px;

    @include vp-1023 {
        margin-top: -10px;
        font-size: 26px;
        line-height: 39px;
    }

    @include vp-767 {
        font-size: 24px;
        line-height: 36px;
        margin-top: -9px;
        margin-bottom: 30px;
    }
}

.products__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}
